.header{background:#424242;box-shadow:0 0 10px rgba(255,254,254,.5);border-radius:0rem 0rem 1rem 1rem;-webkit-border-radius:0rem 0rem 1rem 1rem;-moz-border-radius:0rem 0rem 1rem 1rem;-ms-border-radius:0rem 0rem 1rem 1rem;-o-border-radius:0rem 0rem 1rem 1rem}.header li:before,.header li:after{position:absolute;opacity:0;font-size:25px;top:0px;transition:all .3s;color:var(--sunset7)}.header li:before{content:"(";top:-0.5rem;left:-1rem;transform:translateX(10px)}.header li:after{content:")";top:-0.5rem;right:-1rem;transform:translateX(-10px)}.header li:hover:before,.header li:hover:after{opacity:1;transform:translateX(0px)}.header nav{margin:auto;width:100%;height:2.5rem;display:flex;align-items:center;justify-content:space-between}.header nav ul{display:flex;padding:0 20px;font-size:.9rem;font-weight:500;font-family:"Montserrat",sans-serif;text-transform:uppercase;letter-spacing:3px;width:100%}.header nav li,.header nav img{margin:auto}.header nav li:hover,.header nav img:hover{transform:scale(1.1);-webkit-transform:scale(1.1);-moz-transform:scale(1.1);-ms-transform:scale(1.1);-o-transform:scale(1.1)}.header nav a{color:#fff;text-decoration:none}.header nav a img{margin:.3rem;width:2.5rem}.header__burgerMenu{display:none}@media screen and (max-width: 768px){span{display:none}.header__burgerMenu{display:flex;margin-left:90%;font-size:2rem;cursor:pointer;right:0%}.header__burgerMenu:before{content:"☰"}ul{position:fixed;left:-110%;top:0;flex-direction:column;transition:.25s;background-color:#727272;z-index:100}ul li{padding:.3rem 0}ul li:hover{background:#aaa9a9}.active .header__burgerMenu:before{content:"✕"}.active ul{left:0}}